// 1.  axios
import axios from 'axios'
// 2. axios
const httpRequest = axios.create({
  baseURL: 'http://localhost:8299/', // apibase_url
  timeout: 15000 // 

})
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'
// 3.  request
httpRequest.interceptors.request.use(config => {
  return config
}, error => {
  Promise.reject(error)
})

// 4. response 
httpRequest.interceptors.response.use(response => response, error => {
  console.log('httpRequest:', error)
  if (error && error.response) {
    switch (error.response.status) {
      case 400:
        error.message = ''
        break

      case 403:
        error.message = ','
        break

      case 404:
        error.message = `: ${error.response.config.url}`
        break

      case 500:
        error.message = error.response.data.errorMsg
        break

      default:
        error.message = ''
    }
  }
  return Promise.reject(error)
})
// 5. 
export default httpRequest



