<template>
  <div class="login">
      <img src="../assets/logo.png">
      <div style="width: 30%;">
        <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="" prop="accid">
            <el-input type="text" v-model.trim="ruleForm.accid" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="token">
            <el-input type="password" v-model="ruleForm.token" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
            <el-button @click="resetForm('ruleForm')"></el-button>
          </el-form-item>
        </el-form>
      </div>

    <el-dialog style="text-align: left;" title="" :visible.sync="dialogVisible" width="30%" :close-on-click-modal="false" :close-on-press-escape="false">

      <el-form :model="reruleForm" status-icon :rules="rerules" ref="reruleForm" label-width="100px" style="width: 88%;">
        <el-form-item label="" prop="accid">
          <el-input type="text" v-model.trim="reruleForm.accid" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input type="text" v-model.trim="reruleForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="token">
          <el-input type="password" v-model="reruleForm.token" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="registerAcount"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import SDK from '../assets/plugins/NIM_Web_SDK_v7.6.0.js'
  export default {
    name: 'Home',
    data () {
      var validatePass = (rule, value, callback) => {
        let rep = /^[a-z0-9]+$/
        if (value === '') {
          callback(new Error(''))
        } else if (!rep.test(value)) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value.length < 6) {
          callback(new Error('6'))
        } else {
          callback()
        }
      }
      return {
        dialogVisible: false,
        ruleForm: {
          accid: '',
          token: ''
        },
        rules: {
          accid: [
            { validator: validatePass, trigger: 'blur' }
          ],
          token: [
            { validator: validatePass2, trigger: 'blur' }
          ]
        },
        reruleForm: {
          accid: '',
          token: '',
          name: ''
        },
        rerules: {
          accid: [
            { required: true, message: '', trigger: 'blur' },
            { validator: validatePass, trigger: 'blur' }
          ],
          token: [
            { required: true, message: '', trigger: 'blur' },
            { validator: validatePass2, trigger: 'blur' }
          ],
          name: [
            { required: true, message: '', trigger: 'blur' }
          ]
        }
      }
    },
    mounted () {

    },
    methods: {
      // 
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.handleLogin()
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      // 
      handleLogin() {
        let self = this
        let nim = SDK.NIM.getInstance({
          // debug: true,
          appKey: this.$store.getters.getAppKey,
          account: this.ruleForm.accid,
          token: this.ruleForm.token,
          // onfriends: this.onFriends,
          onconnect: function () {
            self.$message({
              message: '',
              type: 'success'
            })
            // nim.disconnect()
            self.$store.dispatch('setNimWeb', nim)
            self.$store.dispatch('setAccId', self.ruleForm.accid)
            self.$store.dispatch('setToken', self.ruleForm.token)
            self.$router.push({name: 'Chat'})
            // 
          },
          ondisconnect: function (obj) {
            console.log('SDK ', obj)
          },
          onerror: function (error) {
            console.log('SDK ', error)
            self.$message({
              message: '',
              type: 'success'
            })
          }
        })
      },
      onFriends(friends) {
        console.log('', friends)
      },
      // 
      resetForm(formName) {
        this.$refs[formName].resetFields()
        this.dialogVisible = true
        this.$nextTick((e) => {
          this.$refs['reruleForm'].resetFields()
        })
      },
      // 
      registerAcount() {
        this.$refs['reruleForm'].validate((valid) => {
          if (valid) {
            this.createAcount()
          } else {
            return false
          }
        })
      },
      //  api
      createAcount() {
        let url = '/createUser'
        this.http.post(url, this.reruleForm).then((res) => {
          let data = res.data
          if (data && data.statusCode === 200) {
            this.$message({
              message: '',
              type: 'success'
            })
            this.dialogVisible = false
          } else {
            this.$message({
              message: '' + data.statusMsg,
              type: 'error'
            })
          }
        }).catch((e) => {
          this.$message({
            message: e.message,
            type: 'error'
          })
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .login {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
</style>



